import React, { useContext } from 'react';
import { Routes, Route } from 'react-router-dom';
import { message, ConfigProvider, theme as antdTheme } from 'antd';
import { ThemeContext } from './context/ThemeContext';
import Home from './pages/Home';
import Reader from './pages/Reader';
import './styles/global.scss';

/**
 * 
 * @returns {JSX.Element} App
 */
const App = () => {
  const { theme } = useContext(ThemeContext);
  
  // 
  message.config({
    duration: 2,
    maxCount: 3,
  });

  // Ant Design
  const getThemeConfig = () => {
    return {
      token: {
        colorPrimary: '#ff9e7d', // 
        borderRadius: 8, // 
        colorBgContainer: theme === 'dark' ? '#363636' : '#ffffff', // 
        colorText: theme === 'dark' ? '#e8e8e8' : '#333333', // 
        colorBgElevated: theme === 'dark' ? '#444444' : '#ffffff', // 
        colorBgMask: theme === 'dark' ? 'rgba(0,0,0,0.65)' : 'rgba(0,0,0,0.45)', // 
      },
      algorithm: theme === 'dark' ? antdTheme.darkAlgorithm : antdTheme.defaultAlgorithm,
    };
  };
  
  return (
    <ConfigProvider theme={getThemeConfig()}>
      <div className={`app ${theme}`}>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/reader/:fileName" element={<Reader />} />
        </Routes>
      </div>
    </ConfigProvider>
  );
};

export default App; 