import React, { useState, useEffect } from 'react';
import { Empty, Pagination, Spin } from 'antd';
import Header from '../components/Header';
import BookCard from '../components/BookCard';
import { getBooks, getFavorites } from '../api';
import { paginate } from '../utils';
import './Home.scss';

/**
 * 
 * @returns {JSX.Element} Home
 */
const Home = () => {
  const [books, setBooks] = useState([]);
  const [favorites, setFavorites] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchQuery, setSearchQuery] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const pageSize = 12;

  // 
  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        // 
        const [booksRes, favoritesRes] = await Promise.all([
          getBooks(),
          getFavorites()
        ]);

        if (booksRes.success) {
          setBooks(booksRes.data);
        } else {
          throw new Error(booksRes.message || '');
        }

        if (favoritesRes.success) {
          setFavorites(favoritesRes.data);
        } else {
          throw new Error(favoritesRes.message || '');
        }
      } catch (err) {
        console.error(':', err);
        setError(err.message || '');
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  // 
  const handleFavoriteChange = (id, isFavorite) => {
    if (isFavorite) {
      const book = books.find(b => b.id === id);
      if (book) {
        setFavorites(prev => [...prev, { id, title: book.title }]);
      }
    } else {
      setFavorites(prev => prev.filter(item => item.id !== id));
    }
  };

  // 
  const filteredBooks = books
    .filter(book => 
      searchQuery === '' || 
      book.title.toLowerCase().includes(searchQuery.toLowerCase())
    )
    .sort((a, b) => {
      // 
      const aFavorite = favorites.some(f => f.id === a.id);
      const bFavorite = favorites.some(f => f.id === b.id);
      
      if (aFavorite && !bFavorite) return -1;
      if (!aFavorite && bFavorite) return 1;
      
      // 
      return a.title.localeCompare(b.title);
    });

  // 
  const paginatedBooks = paginate(filteredBooks, currentPage, pageSize);
  const totalItems = filteredBooks.length;

  // 
  const handlePageChange = (page) => {
    setCurrentPage(page);
    window.scrollTo(0, 0);
  };

  return (
    <div className="home-page">
      <Header searchQuery={searchQuery} onSearch={setSearchQuery} />
      
      <div className="content-container">
        {loading ? (
          <div className="loading-container">
            <Spin size="large" />
            <p>...</p>
          </div>
        ) : error ? (
          <div className="error-container">
            <p>: {error}</p>
          </div>
        ) : paginatedBooks.length > 0 ? (
          <>
            <div className="books-grid">
              {paginatedBooks.map(book => (
                <BookCard
                  key={book.id}
                  book={book}
                  isFavorite={favorites.some(f => f.id === book.id)}
                  onFavoriteChange={handleFavoriteChange}
                />
              ))}
            </div>
            
            {totalItems > pageSize && (
              <div className="pagination-container">
                <Pagination
                  current={currentPage}
                  pageSize={pageSize}
                  total={totalItems}
                  onChange={handlePageChange}
                  showSizeChanger={false}
                />
              </div>
            )}
          </>
        ) : (
          <Empty 
            description={
              searchQuery ? `"${searchQuery}"` : ""
            } 
          />
        )}
      </div>
    </div>
  );
};

export default Home; 