import React, { useState, useEffect, useRef, forwardRef, useImperativeHandle } from 'react';
import { 
  Drawer, Slider, Switch, Button,
  Tooltip, Divider
} from 'antd';
import { 
  SettingOutlined, ArrowLeftOutlined, 
  BgColorsOutlined, FontSizeOutlined, LineHeightOutlined,
  EyeOutlined, RollbackOutlined,
  PlayCircleOutlined, CheckOutlined
} from '@ant-design/icons';
import './ReaderControls.scss';

/**
 * 
 */
const ReaderControls = forwardRef((props, ref) => {
  const {
    settings,
    updateSetting,
    resetSettings,
    toggleEyeProtection,
    toggleAutoScroll,
    handleBack,
    bookName
  } = props;
  
  const [visible, setVisible] = useState(false);
  const [statusBarVisible, setStatusBarVisible] = useState(true);
  const statusBarTimerRef = useRef(null);

  // 
  useImperativeHandle(ref, () => ({
    // /
    toggleStatusBar: () => {
      // 
      if (visible) return;
      
      const newVisibility = !statusBarVisible;
      setStatusBarVisible(newVisibility);
      
      // 
      if (newVisibility) {
        autoHideStatusBar();
      } else if (statusBarTimerRef.current) {
        // 
        clearTimeout(statusBarTimerRef.current);
      }
    }
  }));

  // 
  const showDrawer = () => {
    setVisible(true);
    // 
    setStatusBarVisible(true);
    // 
    if (statusBarTimerRef.current) {
      clearTimeout(statusBarTimerRef.current);
    }
  };

  // 
  const onClose = () => {
    setVisible(false);
    // 
    autoHideStatusBar();
  };

  // 
  const autoHideStatusBar = () => {
    if (statusBarTimerRef.current) {
      clearTimeout(statusBarTimerRef.current);
    }
    
    statusBarTimerRef.current = setTimeout(() => {
      setStatusBarVisible(false);
    }, 2000); // 2
  };
  
  // 
  useEffect(() => {
    autoHideStatusBar();
    
    return () => {
      if (statusBarTimerRef.current) {
        clearTimeout(statusBarTimerRef.current);
      }
    };
  }, []);

  // 
  const handleColorChange = (color) => {
    updateSetting('background', color.value);
    updateSetting('color', color.textColor);
  };

  // 
  const bgColors = [
    { name: '', value: '#fff8f5', textColor: '#333333' },
    { name: '', value: '#f0f8e6', textColor: '#3a3a3a' },
    { name: '', value: '#faf6e9', textColor: '#5c4b00' },
    { name: '', value: '#f5f5f5', textColor: '#333333' },
    { name: '', value: '#2c2c2c', textColor: '#e8e8e8' }
  ];

  return (
    <div className="reader-controls">
      <div className={`status-bar ${statusBarVisible ? '' : 'hidden'}`}>
        <Button 
          type="text" 
          icon={<ArrowLeftOutlined />} 
          onClick={handleBack}
          className="control-btn"
        />
        
        <div className="book-title">{bookName}</div>
        
        <div className="spacer"></div>
        
        <Button
          type="text"
          icon={<SettingOutlined />}
          onClick={showDrawer}
          className="control-btn"
          title=""
        />
      </div>

      <Drawer
        title=""
        placement="right"
        onClose={onClose}
        open={visible}
        width={320}
        className="settings-drawer"
      >
        <div className="settings-panel">
          <div className="setting-item">
            <div className="setting-title">
              <FontSizeOutlined /> 
            </div>
            <Slider
              min={12}
              max={32}
              value={settings.fontSize}
              onChange={value => updateSetting('fontSize', value)}
            />
            <div className="slider-values">
              <span></span>
              <span></span>
            </div>
          </div>

          <div className="setting-item">
            <div className="setting-title">
              <LineHeightOutlined /> 
            </div>
            <Slider
              min={1.3}
              max={2.5}
              step={0.1}
              value={settings.lineHeight}
              onChange={value => updateSetting('lineHeight', value)}
            />
            <div className="slider-values">
              <span></span>
              <span></span>
            </div>
          </div>

          <div className="setting-item">
            <div className="setting-title">
              <BgColorsOutlined /> 
            </div>
            <div className="color-picker">
              {bgColors.map(color => (
                <div 
                  key={color.value} 
                  className="color-option-wrapper"
                  onClick={() => handleColorChange(color)}
                >
                  <Tooltip title={color.name}>
                    <div
                      className={`color-option ${settings.background === color.value ? 'active' : ''}`}
                      style={{ backgroundColor: color.value }}
                    >
                      {settings.background === color.value && (
                        <CheckOutlined className="check-icon" />
                      )}
                    </div>
                  </Tooltip>
                </div>
              ))}
            </div>
          </div>

          <Divider />

          <div className="setting-item switch-item">
            <div className="setting-title">
              <EyeOutlined /> 
            </div>
            <Switch
              checked={settings.isEyeProtection}
              onChange={toggleEyeProtection}
            />
          </div>

          <div className="setting-item switch-item">
            <div className="setting-title">
              <PlayCircleOutlined /> 
            </div>
            <Switch
              checked={settings.autoScroll}
              onChange={toggleAutoScroll}
            />
          </div>

          {settings.autoScroll && (
            <div className="setting-item">
              <div className="setting-title">
                
              </div>
              <Slider
                min={1}
                max={5}
                value={settings.autoScrollSpeed}
                onChange={value => updateSetting('autoScrollSpeed', value)}
              />
              <div className="slider-values">
                <span></span>
                <span></span>
              </div>
            </div>
          )}

          <Divider />

          <div className="setting-item">
            <Button 
              icon={<RollbackOutlined />} 
              onClick={resetSettings}
              block
            >
              
            </Button>
          </div>
        </div>
      </Drawer>
    </div>
  );
});

export default ReaderControls; 